@keyframes fade-in {
  0% {
    display: none;
    opacity: 0;
  }
  1% {
    display: block;
    opacity: 0;
  }
  100% {
    display: block;
    opacity: 1;
  }
}
@keyframes fade-out {
  0% {
    display: block;
    opacity: 1;
  }
  99% {
    display: block;
    opacity: 0;
  }
  100% {
    display: none;
    opacity: 0;
  }
}
@keyframes _on_opacity {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes header_index_show {
  0% {
    display: none;
  }
  1% {
    display: block;
    opacity: 0;
    top: -30px;
  }
  100% {
    top: 0;
    opacity: 1;
    display: block;
  }
}
@keyframes header_index_hide {
  0% {
    top: 0;
    opacity: 1;
    display: block;
  }
  99% {
    top: -30px;
    opacity: 0;
    display: block;
  }
  100% {
    top: -30px;
    opacity: 0;
    display: none;
  }
}
@keyframes header_search_show {
  0% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(64px);
  }
}
@keyframes header_search_hide {
  0% {
    transform: translateY(64px);
  }
  100% {
    transform: translateY(0);
  }
}
/* Slider */
.slick-loading .slick-list {
  background: #fff url("./ajax-loader.gif") center center no-repeat;
}

/* Icons */
@font-face {
  font-family: "slick";
  src: url("./fonts/slick.eot");
  src: url("./fonts/slick.eot?#iefix") format("embedded-opentype"), url("./fonts/slick.woff") format("woff"), url("./fonts/slick.ttf") format("truetype"), url("./fonts/slick.svg#slick") format("svg");
  font-weight: normal;
  font-style: normal;
}
/* Arrows */
.slick-prev,
.slick-next {
  padding: 0;
  border: 0 none;
  outline: none;
  position: absolute;
  line-height: 0px;
  font-size: 0px;
  color: transparent;
  pointer-events: auto;
  display: block;
  width: 28px;
  height: 28px;
  cursor: pointer;
  top: 50%;
  margin-top: -14px;
  z-index: 10;
}
@media screen and (max-width: 999px) {
  .slick-prev,
.slick-next {
    width: 19.6px;
    height: 19.6px;
  }
}
.slick-prev::before,
.slick-next::before {
  content: "";
  display: block;
  background: url("/common/img/icon/arrow/slick.webp") 0 0 no-repeat transparent;
  background-size: 28px 28px;
  width: 28px;
  height: 28px;
  background-color: #169296;
  transition: transform 0.05s cubic-bezier(0.23, 1, 0.32, 1);
  position: absolute;
  top: 50%;
  left: 50%;
  margin-left: -14px;
  margin-top: -14px;
}
.slick-prev:hover, .slick-prev:focus,
.slick-next:hover,
.slick-next:focus {
  outline: none;
}
.slick-prev:hover::before, .slick-prev:focus::before,
.slick-next:hover::before,
.slick-next:focus::before {
  transform: translateX(10%);
}
.slick-prev.slick-disabled,
.slick-next.slick-disabled {
  position: absolute;
  pointer-events: none;
}
.slick-prev.slick-disabled::before,
.slick-next.slick-disabled::before {
  background-color: #a1c9ca;
}

.slick-prev {
  transform: rotate(180deg);
  left: 0;
}
[dir=rtl] .slick-prev {
  left: auto;
  right: 0;
}
.slick-prev.slick-disabled {
  left: auto;
}

.slick-next {
  left: auto;
  right: 0;
}
[dir=rtl] .slick-next {
  right: 0;
  left: auto;
}
.slick-next.slick-disabled {
  left: auto;
}

/* Dots */
.slick-dots {
  list-style: none;
  display: block;
  text-align: center;
  padding: 0;
  margin: 0;
  width: 100%;
  pointer-events: none;
}
@media only screen and (max-width: 767px) {
  .slick-dots {
    bottom: 0;
  }
}
.slick-dots li {
  position: relative;
  display: inline-block;
  height: 8px;
  margin: 0 6px;
  padding: 0;
  cursor: pointer;
}
.slick-dots li button {
  pointer-events: auto;
  border: 0;
  outline: none;
  line-height: 0px;
  color: transparent;
  font-size: 0px;
  padding: 0;
  position: relative;
  top: 0;
  left: 0;
  display: block;
  width: 8px;
  height: 8px;
  cursor: pointer;
  background-color: #b4c1ce;
  transition: transform 0.6s cubic-bezier(0.075, 0.82, 0.165, 1);
  border-radius: 4px;
  box-shadow: 0 none;
}
.slick-dots li button:focus {
  outline: none;
}
.slick-dots li.slick-active button,
.slick-dots li button:hover,
.slick-dots li button:active {
  outline: none;
  background-color: #194776;
}