@charset 'UTF-8';

@import '../../import/var';
@import '../../import/mixins';
@import '../../import/animation';

// Default Variables

// Slick icon entity codes outputs the following
// '\2190' outputs ascii character '←'
// '\2192' outputs ascii character '→'
// '\2022' outputs ascii character '•'

$slick-font-path: './fonts/' !default;
$slick-font-family: 'slick' !default;
$slick-loader-path: './' !default;
$slick-arrow-color: #fff !default;
$slick-dot-color: #000 !default;
$slick-dot-color-active: $slick-dot-color !default;
$slick-prev-character: '\2190' !default;
$slick-next-character: '\2192' !default;
$slick-dot-character: '\2022' !default;
$slick-dot-size: 6px !default;
$slick-opacity-default: 0.75 !default;
$slick-opacity-on-hover: 1 !default;
$slick-opacity-not-active: 0.25 !default;

@function slick-image-url($url) {
	@if function-exists(image-url) {
		@return image-url($url);
	} @else {
		@return url($slick-loader-path + $url);
	}
}

@function slick-font-url($url) {
	@if function-exists(font-url) {
		@return font-url($url);
	} @else {
		@return url($slick-font-path + $url);
	}
}

/* Slider */

.slick-list {
	.slick-loading & {
		background: #fff slick-image-url('ajax-loader.gif') center center no-repeat;
	}
}

/* Icons */
@if $slick-font-family == 'slick' {
	@font-face {
		font-family: 'slick';
		src: slick-font-url('slick.eot');
		src: slick-font-url('slick.eot?#iefix') format('embedded-opentype'), slick-font-url('slick.woff') format('woff'), slick-font-url('slick.ttf') format('truetype'), slick-font-url('slick.svg#slick') format('svg');
		font-weight: normal;
		font-style: normal;
	}
}




$width: 28px;
$height: 28px;

/* Arrows */
.slick-prev,
.slick-next {
	padding: 0;
	border: 0 none;
	outline: none;
	position: absolute;
	line-height: 0px;
	font-size: 0px;
	// background: transparent;
	color: transparent;
	pointer-events: auto;

	display: block;
	width: $width;
	height: $height;
	cursor: pointer;
	top: 50%;
	margin-top: ($width / 2) * -1;
	z-index: 10;
	// background: rgba(#7bdff2, .9);
	// b4c1ce
	// 194776


	@media #{$sp} {
		width: $width * .7;
		height: $height * .7;
	}

	&::before {
		@include bg-block('/common/img/icon/arrow/slick.webp', $width, $height);
		// background-color: #b4c1ce;
		background-color: #169296;
		transition: transform .05s $easeOutQuint;
		position: absolute;
		top: 50%;
		left: 50%;
		margin-left: - ($width / 2);
		margin-top: - ($height / 2);
		// @media #{$sp} {
		// 	width: 40px;
		// 	height: 40px;
		// 	margin-left: -20px;
		// 	margin-top: -20px;
		// 	background-size: contain;

		// }
	}

	&:hover,
	&:focus {
		outline: none;
		&::before {
			transform: translateX(10%);
		}
	}

	&.slick-disabled {
		position: absolute;
		pointer-events: none;
		&::before {
			background-color: #a1c9ca;
		}
	}
}

$arrow_padding: 0;
.slick-prev {
	transform: rotate(180deg);
	left: $arrow_padding;
	[dir='rtl'] & {
		left: auto;
		right: $arrow_padding;
	}
	&.slick-disabled {
		left: auto;
	}
}

.slick-next {
	left: auto;
	right: $arrow_padding;
	[dir='rtl'] & {
		right: $arrow_padding;
		left: auto;
	}
	&.slick-disabled {
		left: auto;
	}
}

/* Dots */

// .slick-dotted.slick-slider {
// 	margin-bottom: 30px;
// }

.slick-dots {
	$width: 8px;
	$height: 8px;
	$margin: 12px;

	// height: $height;
	list-style: none;
	display: block;
	text-align: center;
	padding: 0;
	margin: 0;
	width: 100%;
	pointer-events: none;

	// position: absolute;
	// left: 50%;
	// bottom: 120px;
	// z-index: 11;
	// transform: translateX(-50%);

	@media only screen and (max-width: 767px) {
		bottom: 0;
	}

	li {
		position: relative;
		display: inline-block;
		// width: A;
		height: $height;
		margin: 0 ($margin / 2);
		padding: 0;
		cursor: pointer;

		button {
			pointer-events: auto;
			// @include sprite2($icon-slick-dot);
			border: 0;
			// background: transparent;
			outline: none;
			line-height: 0px;
			color: transparent;
			font-size: 0px;
			padding: 0;
			position: relative;
			top: 0;
			left: 0;

			display: block;
			width: $width;
			height: $height;
			cursor: pointer;
			// background-color: #fff;
			background-color: #b4c1ce;
			transition: transform .6s $easeOutCirc;
			border-radius: $width / 2;
			box-shadow: 0 none;

			&:focus {
				outline: none;
			}
		}
		&.slick-active button,
		button:hover,
		button:active {
			outline: none;
			background-color: #194776;
		}
	}
}
