@mixin columnsb($column, $padding: 20px, $max_width: null) {
	@include clearfix;

	> * {
		@if $max_width != null {
			width: floor(($max_width - ($padding * ($column - 1))) / $column);
		} @else {
			width: 100% / $column;
		}

		float: left;
		box-sizing: border-box;
	}

	@for $i from 1 through ($column) {
		$ii: $column - $i;

		@if ($column != $i) {
			> *:nth-child(#{$column}n + #{$i}) {
				@if ($i == 1) {
					clear: left;
					padding-right: floor($padding / $column * $ii);
				} @else {
					padding-left: floor($padding / $column * ($i - 1));
					padding-right: floor($padding / $column * $ii);
				}
			}
		} @else {
			> *:nth-child(#{$column}n) {
				padding-left: floor($padding / $column * ($i - 1));
			}
		}
	}
}

@mixin flex_columns($column, $width, $padding, $is_cancel: false) {
	display: flex;
	flex-wrap: wrap;
	width: 100%;

	> * {
		width: $width;
		margin-right: $padding;

		@if $is_cancel {
			padding-left: 0;
			padding-right: 0;
			float: none;
		}

		&:nth-child(#{$column}n) {
			margin-right: 0;
		}
	}
}

/* simple clearfix */
%clearfix {
	&::before,
	&::after {
		content: "";
		display: table;
	}

	&::after {
		clear: both;
	}
}

@mixin clearfix {
	&::before,
	&::after {
		content: "";
		display: table;
	}

	&::after {
		clear: both;
	}
}

@mixin on_opacity {
	&:hover {
		text-decoration: none;
		opacity: 0.7;
		transition: 0.2s opacity;
	}
}

/**************************
@mixin for sprite
*/

@mixin sprite2-width($sprite) {
	width: nth($sprite, 5) / 2;
}

@mixin sprite2-height($sprite) {
	height: nth($sprite, 6) / 2;
}

@mixin sprite2-position($sprite) {
	$sprite-offset-x: nth($sprite, 3) / 2;
	$sprite-offset-y: nth($sprite, 4) / 2;

	background-position: $sprite-offset-x $sprite-offset-y;
}

@mixin sprite2-size($sprite) {
	background-size: nth($sprite, 7) / 2 nth($sprite, 8) / 2;
}

@mixin sprite2($sprite) {
	@include sprite-image($sprite);
	@include sprite2-position($sprite);
	@include sprite2-width($sprite);
	@include sprite2-height($sprite);
	@include sprite2-size($sprite);
}

@mixin icon-sprite2-inline($sprite, $top: "middle", $before: "before", $is_block: false, $margin-right: 0.5em) {
	@include icon-sprite2($sprite, $top, $before, $is_block, $margin-right);
}

@mixin icon-sprite2($sprite, $top: "middle", $before: "before", $is_block: true, $margin-right: 0.5em) {
	position: relative;
	left: 0;

	@if ($is_block == true) {
		&::#{$before} {
			@include sprite2($sprite);

			content: "";
			display: block;
			position: absolute;
			left: 0;

			@if $top == "middle" {
				top: 50%;
				transform: translateY(-50%);
			} @else {
				top: $top;
			}
		}
	} @else {
		&::#{$before} {
			@include sprite2($sprite);

			content: "";
			display: inline-block;

			@if $before == before {
				margin-right: $margin-right;
			} @else {
				margin-left: $margin-right;
			}

			@if $top != "middle" {
				position: relative;
				top: $top;
				left: 0;
			}
		}
	}
}

