@mixin default_image($width: 100%) {
	// border-radius: $border_radius;
	display: block;
	margin-left: auto;
	margin-right: auto;
	max-width: $width;
}

@mixin set($width, $padding) {
	.resize.right {
		@include columns_flex($width, right, $padding);
	}

	.fix.left {
		@include columns_fix($width, left);
	}

	.resize.left {
		@include columns_flex($width, left, $padding);
	}

	.fix.right {
		@include columns_fix($width, right);
	}

	.fix,
	.resize {
		> img:not(.cancel) {
			@include default_image($width);
		}
	}
}

@mixin default_scroll_anime($delay: 0s) {
	transition: all 1.4s $easeOutExpo $delay;

	&.init {
		opacity: 0;
		transform: translateY(-5%);
	}
}

@mixin cancel() {
	padding: 0;
	margin: 0;
	list-style: none;
	width: inherit;
	height: inherit;
	border: 0 none;
	color: inherit;
	vertical-align: baseline;
	text-align: inherit;
	font-size: inherit;
	font-weight: normal;
	line-height: inherit;
	overflow: visible;
	border-radius: 0;
	position: static;
	left: auto;
	top: auto;
	clear: inherit;
	content: normal;
	min-height: initial;
	max-height: initial;
	float: none;
	transform: none;
	background: none center center / center center repeat-none transparent;
	box-sizing: content-box;
}

@mixin cancel_all() {
	@include cancel;

	&::before,
	&::after {
		@include cancel;
	}
}

@mixin cancel_list() {
	.main_content & {
		padding-left: 0;

		li {
			margin-bottom: 0;

			&::before {
				display: none;
			}
		}
	}
}

@mixin width_100vw {
	width: 100vw;
	max-width: none;
	min-width: 0;
	position: relative;
	top: 0;
	left: -50vw;
	right: -50vw;
	margin-right: calc(50%);
	margin-left: calc(50%);
	// margin-right: calc(50% - 50vw);
	// margin-left: calc(50% - 50vw);
}

@mixin sp_unindent {
	margin-left: -1 * $sp_padding;
	margin-right: -1 * $sp_padding;
	// width: 100vw;
}

@mixin pc_unindent {
	margin-left: -1 * $pc_padding;
	margin-right: -1 * $pc_padding;
}

// https://terkel.jp/archives/2012/12/decimal-digits-and-rounding-sass-function/
// Round (四捨五入)
@function round-decimal($number, $digits: 0) {
	@return to-fixed($number, $digits, "round");
}

// Ceil (切り上げ)
@function ceil-decimal($number, $digits: 0) {
	@return to-fixed($number, $digits, "ceil");
}

// Floor (切り捨て)
@function floor-decimal($number, $digits: 0) {
	@return to-fixed($number, $digits, "floor");
}

@function to-fixed($number, $digits: 0, $round: "round") {
	$n: 1;
	// $number must be a number
	@if type-of($number) != number {
		@warn '#{ $number } is not a number.';

		@return $number;
	}
	// $digits must be a unitless number
	@if type-of($digits) != number {
		@warn '#{ $digits } is not a number.';

		@return $number;
	} @else if not unitless($digits) {
		@warn '#{ $digits } has a unit.';

		@return $number;
	}

	@for $i from 1 through $digits {
		$n: $n * 10;
	}

	@if $round == "round" {
		@return round($number * $n) / $n;
	} @else if $round == "ceil" {
		@return ceil($number * $n) / $n;
	} @else if $round == "floor" {
		@return floor($number * $n) / $n;
	} @else {
		@warn '#{ $round } is undefined keyword.';

		@return $number;
	}
}
